import {
    skip,
    skipNode,
    importNode,
    currentElement,
    currentPointer,
    elementOpen,
    elementOpenStart,
    elementOpenEnd,
    elementClose,
    elementVoid,
    attr,
    text,
    applyAttr,
    applyProp,
    patch,
    patchOuter,
    symbols,
    attributes,
    notifications,
} from 'incremental-dom/index.js';

const CALLBACKS = {
    beforeOpen: [],
    afterOpen: [],
    beforeClose: [],
    afterClose: [],
    beforeText: [],
    afterText: [],
    beforeAttr: [],
    afterAttr: [],
};

function trigger(clb, ...args) {
    for (let i = 0, len = CALLBACKS[clb].length; i < len; i++) {
        let res = CALLBACKS[clb][i].call(IDOM, ...args);
        if (res === false) {
            return false;
        }
    }
    return true;
}

/**
 * IncrementalDOM wrapper.
 */
export class IDOM {
    /**
     * @private
     */
    static close(...args) { return close(...args); }
    /**
     * @private
     */
    static skip(...args) { return skip(...args); }
    /**
     * @private
     */
    static skipNode(...args) { return skipNode(...args); }
    /**
     * @private
     */
    static importNode(...args) { return importNode(...args); }
    /**
     * @private
     */
    static currentElement(...args) { return currentElement(...args); }
    /**
     * @private
     */
    static currentPointer(...args) { return currentPointer(...args); }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementOpen IncrementalDOM.elementOpen}
     */
    static elementOpen(...args) {
        if (trigger('beforeOpen', ...args)) {
            let res = elementOpen(...args);
            let clb = trigger('afterOpen', res);
            if (clb === false) {
                this.elementClose(args[0]);
                return false;
            }
            return res;
        }
    }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementOpenStart IncrementalDOM.elementOpenStart}
     */
    static elementOpenStart(...args) {
        if (trigger('beforeOpen', ...args)) {
            let res = elementOpenStart(...args);
            return res || true;
        }
    }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementOpenEnd IncrementalDOM.elementOpenEnd}
     */
    static elementOpenEnd(...args) {
        let res = elementOpenEnd(...args);
        let clb = trigger('afterOpen', res);
        if (clb === false) {
            this.elementClose(args[0]);
            return false;
        }
        return res;
    }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementClose IncrementalDOM.elementClose}
     */
    static elementClose(...args) {
        if (trigger('beforeClose', ...args)) {
            let res = elementClose(...args);
            return trigger('afterClose', res) || res;
        }
    }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/elementVoid IncrementalDOM.elementVoid}
     */
    static elementVoid(...args) {
        if (trigger('beforeOpen', ...args)) {
            let res = elementVoid(...args);
            return trigger('afterOpen', res) || res;
        }
    }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/attr IncrementalDOM.attr}
     */
    static attr(...args) {
        if (trigger('beforeAttr', ...args)) {
            let res = attr(...args);
            return trigger('afterAttr', res) || res;
        }
    }
    /**
     * Wrap the IncrementalDOM.text function with some arguments checks.
     * @see {@link http://google.github.io/incremental-dom/#api/text IncrementalDOM.text}
     * @param {String} str The text to render.
     */
    static text(str) {
        if (trigger('beforeText', str)) {
            if (typeof str === 'undefined') {
                str = '';
            }
            let res = text(str);
            return trigger('afterText', res) || res;
        }
    }
    /**
     * @private
     */
    static applyAttr(...args) { return applyAttr(...args); }
    /**
     * @private
     */
    static applyProp(...args) { return applyProp(...args); }
    /**
     * @see {@link http://google.github.io/incremental-dom/#api/patch IncrementalDOM.patch}
     */
    static patch(...args) { return patch(...args); }
    /**
     * @private
     */
    static patchOuter(...args) { return patchOuter(...args); }
    /**
     * @private
     */
    static get symbols() { return symbols; }
    /**
     * @private
     */
    static get attributes() { return attributes; }
    /**
     * @private
     */
    static get notifications() { return notifications; }

    static beforeElementOpen(cb) {
        CALLBACKS.beforeOpen.push(cb);
    }

    static afterElementOpen(cb) {
        CALLBACKS.afterOpen.push(cb);
    }

    static beforeElementClose(cb) {
        CALLBACKS.beforeClose.push(cb);
    }

    static afterElementClose(cb) {
        CALLBACKS.afterClose.push(cb);
    }

    static beforeText(cb) {
        CALLBACKS.beforeText.push(cb);
    }

    static afterText(cb) {
        CALLBACKS.afterText.push(cb);
    }

    static beforeAttr(cb) {
        CALLBACKS.beforeAttr.push(cb);
    }

    static afterAttr(cb) {
        CALLBACKS.afterAttr.push(cb);
    }
}
